import Vue from "vue";
import { Loading } from "element-ui";
//数组的新增方法
Array.prototype.remove = function(val) {
    var index = this.indexOf(val);

    if (index > -1) {
        this.splice(index, 1);
    }
};
//loading状态，message:提示信息，isClosing:是否自动关闭，默认false(不自动关闭)
Vue.prototype.openLoading = function(opt) {
    if (!opt) {
        opt = {};
    }
    if (typeof opt.message == "undefined") {
        opt.message = "正在加载...";
    }
    if (typeof opt.isClosing == "undefined") {
        opt.isClosing = false;
    }

    const loading = Loading.service({
        lock: true,
        text: opt.message,
        spinner: "el-icon-loading",
        background: "rgba(255,255,255,0)",
        customClass: "loadingMask",
    });
    if (opt.isClosing) {
        setTimeout(function() {
            loading.close(); // 关闭遮罩层
        }, 2000);
    }
    return loading;
};
String.prototype.endWith = function(endStr) {
    if (endStr) {
      var d = this.length - endStr.length;
      return d >= 0 && this.lastIndexOf(endStr) == d;
    } else {
      return "";
    }
  };
  
  // 当前环境ip端口号
  Vue.prototype.getPrefixUrl = function() {
    var pathName=window.document.location.pathname;
    var curWwwPath=window.document.location.href;
    var pos=curWwwPath.indexOf(pathName);
    var prefixUrl=curWwwPath.substring(0,pos) + GLOBAL_CONFIG.publicPath+'serviceManagViewMap';
    return prefixUrl
  }
  