import Axios from "axios";
import qs from "qs";
import ElementUI, { Loading } from "element-ui";
import "element-ui/lib/theme-chalk/index.css";

let http = Axios.create({
  headers: {
    "content-type": "application/x-www-form-urlencoded; charset=UTF-8",
  },
});

let options = {
  lock: true,
  text: "处理中，请稍等",
  spinner: "el-icon-loading",
  background: "rgba(255,255,255,0)",
  customClass: "loadingMask",
};

http.defaults.baseURL = GLOBAL_CONFIG.baseUrl;
// 添加请求拦截器
http.interceptors.request.use(
  function(config) {
    let token = sessionStorage.getItem(GLOBAL_CONFIG.token);
    if (token) {
      config.headers.Authorization = sessionStorage.getItem(GLOBAL_CONFIG.token);
    }

    config.url = config.baseURL + config.url;
    config.data = qs.stringify(config.data);
    // 在发送请求之前做些什么

    return config;
  },
  function(error) {
    console.log(error);
    // 对请求错误做些什么
    // return Promise.reject(error)
  }
);
// 添加响应拦截器
http.interceptors.response.use(
  function(response) {
    if (response.data.code) {
      if (response.data.code == 1) {
        ElementUI.Message({
          showClose: false,
          message: response.data.message,
          type: "error",
          offset: 100,
          duration: 2000,
        });
        return;
      } else if (response.data.code == 401) {
        // window.location.href = GLOBAL_CONFIG.tymh_login;
        window.location.href = GLOBAL_CONFIG.baseLogin;
        return;
      }
      ElementUI.Message({
        showClose: false,
        message: "error",
        type: "error",
        offset: 100,
        duration: 1000,
      });
      return;
    }
    // 对响应数据做点什么
    return response;
  },
  function(error) {
    // 对响应错误做点什么
    console.log("超时");
    return;
  }
);
export default {
  //responseType：'blob'    'arraybuffer'   'json'
  get(url, params = {}, responseType = "json") {
    return http({
      url,
      params,
      method: "get",
      responseType,
      timeout: 5000,
    });
  },
  post(url, data = {}) {
    return http({
      url,
      data,
      method: "post",
      timeout: 5000,
    });
  },
  longGet(url, params = {}, responseType = "json") {
    let loading = Loading.service(options);
    return http({
      url,
      params,
      method: "get",
      responseType,
      timeout: 300000,
    }).then((res) => {
      loading.close();
      return res;
    });
  },
  longPost(url, data = {}) {
    let loading = Loading.service(options);
    return http({
      url,
      data,
      method: "post",
      timeout: 300000,
    }).then((res) => {
      loading.close();
      return res;
    });
  },
  //获取枚举值
  getMapData(value) {
    return this.post(GLOBAL_CONFIG.ssjd + "/sys/getXtzdDataByZdzbs", {
      zdzbs: value,
    }).then((res) => {
      if (res && res.data.data) {
        let temp = res.data.data;
        temp.forEach((element, index) => {
          element.sid = element.key;
        });
        return temp;
      } else {
        return [];
      }
    });
  },
  //将枚举的数据转换成map
  transfromMapToArr(value) {
    let result = {};
    return this.getMapData(value).then((res) => {
      res.forEach((element) => {
        result[element.key] = element.value;
      });
      return result;
    });
  },
  //获取行政区划
  getDivisionArea() {
    return this.post(GLOBAL_CONFIG.ssjd + "/sys/getXzqhbData").then((res) => {
      if (res && res.data.data) {
        return res.data.data;
      }
    });
  },
    //一个并发请求，一次可以请求多个 http请求
    multiplePost(option) {
      let arr = [],
        keys = [];
      for (let key in option) {
        keys.push(key);
        arr.push(option[key]);
      }
  
      return Axios.all(arr).then(
        Axios.spread(function() {
          let result = {};
          for (let i = 0; i < arguments.length; i++) {
            let item = arguments[i];
            if (item) {
              if (item.data && item.data.data) {
                result[keys[i]] = item.data.data;
              } else {
                result[keys[i]] = item;
              }
            }
          }
          return result;
        })
      );
    },
};
